.owner-info {
  grid-area: owner;
}

.owner-info__title {
  position: relative;

  @media (min-width: @tablet) {
  }
}

.owner-info__title::before,
.comments-field__title::before,
.additional-checkbox__title::before {
  position: absolute;
  content: "";
  width: 205px;
  height: 2px;
  background-color: @main-color;
  left: 91px;
  top: 16px;
  z-index: -1;

  @media (min-width: @tablet) {
    top: 23px;
    left: -34px;
    width: 750px;

}
  @media (min-width: @desktop) {
    width: 1200px;
  }
}

.owner-info__container {
  display: flex;
  flex-direction: column;

  @media (min-width: @desktop) {
    flex-direction: row;
  }
}

.owner-info__item--email {
  @media (min-width: @desktop) {
    margin-right: 78px;
  }
}

.owner-info__input-mail {
  width: 172px;
  background-image: url(../../images/icon-envelope.svg);
  background-repeat: no-repeat;
  background-size: 25px 20px;
  background-position: 95% 50%;

  @media (min-width: @tablet) {
    width: 580px;
  }
  @media (min-width: @desktop) {
    width: 430px;
  }

  &:focus {
    border: 1px solid #444444;
    border-radius: 0;
    background-image: url(../../images/icon_emailgreen.svg);
  }
}

.owner-info__input-phone {
  width: 153px;
  background-image: url(../../images/icon-phone.svg);
  background-repeat: no-repeat;
  background-size: 25px 20px;
  background-position: 95% 50%;
  @media (min-width: @tablet) {
    width: 558px;
  }

  @media (min-width: @desktop) {
    width: 430px;
  }
}
